import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("/RU-SIRS/")
    val router = Router()
    router.register("/") { io.github.dennistsar.sirs_kobweb.pages.HomePage() }
    router.register("/data") { io.github.dennistsar.sirs_kobweb.pages.Data() }
    router.register("/fakedata") { io.github.dennistsar.sirs_kobweb.pages.FakeData() }
    router.register("/fakeindex") { io.github.dennistsar.sirs_kobweb.pages.FakeIndex() }

    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.pages.PageTitleStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.sections.FooterStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.MainNavStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.MainGridAreaStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.InfoBubbleStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.GridRowStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.ProfNameStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.options.ExtraOptionStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.HeaderStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.AveragesBoxStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.FaOutlineStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.SearchBarStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.widgets.BarGraphStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.widgets.SelectStyle)
        ctx.theme.registerComponentStyle(io.github.dennistsar.sirs_kobweb.components.widgets.LogoStyle)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.pages.SubHeadVariant)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.pages.ActionButtonVariant)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.components.sections.OppositeLinkVariant)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.SideNavVariant)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.MobileNavVariant)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.ProfScoresGridVariant)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.EvenRowVariant)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.OddRowVariant)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.AveRowVariant)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.SpanTextHeaderVariant)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.components.sections.dataPage.QuestionSelectVariant)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.components.widgets.PillButtonVariant)
        ctx.theme.registerComponentVariants(io.github.dennistsar.sirs_kobweb.components.widgets.PillButtonSelectedVariant)
        io.github.dennistsar.sirs_kobweb.updateTheme(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin))

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(AppGlobalsLocal provides mapOf("title" to "RU-SIRS")) {
            io.github.dennistsar.sirs_kobweb.MyApp {
                com.varabyte.kobweb.silk.defer.renderWithDeferred { router.renderActivePage() }
            }
        }
    }
}
