package io.github.dennistsar.sirs_kobweb.components.sections.dataPage

import androidx.compose.runtime.Composable
import androidx.compose.runtime.SideEffect
import androidx.compose.runtime.key
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.Width
import com.varabyte.kobweb.compose.css.functions.min
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.addVariant
import com.varabyte.kobweb.silk.components.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.components.text.SpanTextStyle
import io.github.dennistsar.sirs_kobweb.components.widgets.CustomDropDown
import io.github.dennistsar.sirs_kobweb.components.widgets.SelectStyle
import io.github.dennistsar.sirs_kobweb.misc.UsefulQuestions
import io.github.dennistsar.sirs_kobweb.misc.UsefulQuestionsShort
import io.github.dennistsar.sirs_kobweb.misc.jsBalanceTextById
import io.github.dennistsar.sirs_kobweb.misc.smallCapsFont
import org.jetbrains.compose.web.css.cssRem
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px

val HeaderStyle by ComponentStyle {
    base { Modifier.width(95.percent) }
    Breakpoint.LG { Modifier.width(70.percent) }
}

val SpanTextHeaderVariant by SpanTextStyle.addVariant {
    base {
        Modifier
            .fontWeight(FontWeight.Bold)
            .smallCapsFont()
            .fontSize(1.5.cssRem)
            .lineHeight(1.75.cssRem)
    }
    Breakpoint.MD {
        Modifier
            .fontSize(1.75.cssRem)
            .lineHeight(2.cssRem)
    }
    Breakpoint.LG {
        Modifier
            .fontSize(2.cssRem)
            .lineHeight(2.25.cssRem)
    }
}

val QuestionSelectVariant by SelectStyle.addVariant {
    base {
        Modifier.fontSize(1.cssRem).width(min(400.px, 90.percent))
    }
    Breakpoint.MD {
        Modifier.width(Width.Unset)
    }
}

@Composable
fun QuestionHeader(
    selectedQ: Int,
    modifier: Modifier = Modifier,
    onSelectedQDropDownChange: (Int) -> Unit,
) {
    Column(
        modifier.fillMaxWidth().rowGap(0.5.cssRem),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        key(selectedQ) { // needed for jsBalanceTextById() to work upon text change
            val id = "survey-q"
            Box(HeaderStyle.toModifier(), Alignment.Center) {
                SpanText(
                    "\"${UsefulQuestions[selectedQ]}\"",
                    Modifier.id(id),
                    SpanTextHeaderVariant,
                )
                SideEffect {
                    jsBalanceTextById(id)
                }
            }
        }
        CustomDropDown(
            hint = "Choose another question...",
            selectVariant = QuestionSelectVariant,
            list = UsefulQuestionsShort,
            onSelect = { onSelectedQDropDownChange(UsefulQuestionsShort.indexOf(it)) },
        )
    }
}